
body {
  width: 8 * $size;
  margin: 0 auto;
}
  
.calendar {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  @for $i from 1 through 31 {
    &__day:nth-child(n+#{$i}):before {
      display: flex;
      justify-content: center;
      line-height: $size;
      content: "#{$i}";
      font-family: Arial, Helvetica, sans-serif;
      font-size: 30px;
    }
  }
  &__day {
    @include cell-size($size);
  }
  &__day:hover {
    @include cell-size($size);
    background-color: rgb(212, 132, 132);
    transform: translateY(-20px);
    transition: 0.5s ease-in-out;
  }
  &--month-length {
    @for $index from 28 through 31 {
      &-#{$index} > :nth-child(n + #{$index + 1}) {
        visibility: hidden;
      }
    }
  }
  &--start-day {
    @each $index , $day in $days {
      &-#{$day} > :first-child {
        margin-left: $index * ($size + ($calendar-gap));
      }
    }
  }
}
